	module.exports = {"data":{"markdownRemark":{"html":"<p><a href=\"/dynabook-sslx190\">メインPC</a>故障時の代役として急遽購入した久々のデスクPC。中古で 25,800 円と性能の割には破格値で購入。ただビジネスPCにありがちなVGA性能の不足で頭を抱えることに。逆を返せば制作物の軽量化用実験PCとして使えるかも？ 2 ヵ月でメインPCが復帰したためプリントサーバ兼ファイルサーバとして、またサブPCとして低スペック環境テストや CD-R 大量生産時などPC1台じゃ追いつかない場合などに活躍中。 2009年5月、引っ越しの際豪雨で水没のため逝去。HDDはサルベージ出来たが、PC1台減ったのは痛いな……。</p>\n<h2>当時はまったPCゲーム</h2>\n<p>東方モノ全般</p>\n<h2>製作物・他やってたこと</h2>\n<ul>\n<li>\n<p><a href=\"/!/thC/\">東方夢終劇</a></p>\n<h2>性能</h2>\n</li>\n<li>\n<p>CPU:intel Pentium 4(Willamette/1.9GHz)</p>\n</li>\n<li>\n<p>RAM:768MB(PC-3200)</p>\n</li>\n<li>\n<p>VGA:intel 845GL(onBoard/VRAM48MB)</p>\n</li>\n<li>\n<p>HDD:40GB</p>\n</li>\n<li>\n<p>CDD:CD-RW</p>\n</li>\n<li>\n<p>FDD:3.5inch(3mode)×1</p>\n</li>\n<li>\n<p>NET:100Base-TX/56kbpsFaxModem</p>\n</li>\n<li>\n<p>SOUND:onBoard</p>\n</li>\n<li>\n<p>OS:WindowsXP SP2</p>\n</li>\n<li>\n<p>2001年クラスの最新スペック</p>\n</li>\n</ul>","frontmatter":{"cover":null,"date":"2006-06-15T21:00:00+09:00","strDate":"2006/6/15","title":"(2006-2009) IBM NetVista M42Slim"}}},"pathContext":{"path":"/ja/blog/2006/06/15-netvista-m42slim/","langKey":"ja"}}

